// settings and defaults
$color-link-blue:                 #0071ad;
$color-blue-10:                   #e8f4fa;
$color-text-gray:                 #767676; // lightest accessible gray on white
$color-black-77:                  #3b3b3b;
$color-border:                    #b8b8b8; // taken from form.css


:root{
  --form-field-border-color: #b8b8b8;
  --gutter: 32px;
  --gap: 16px;
  --margin-fields: .75em;
}
body{
  line-height: 1.45;
}

#cke_edit-field-caption-0-value{
  width: auto !important; // without this primary media and caption on events will not sit side by side on desktop
}

// term reference tree
.region-content ul.term-reference-tree-level
,.term-reference-tree-level
{
  margin: 2px 0 0 -24px;
}
.field--widget-term-reference-tree{
  ul{
    li{
      margin-bottom: 2px;
    }
  }
}
.term-reference-tree-button {
  visibility: hidden;
}
.no-term-reference-tree-button {
  background-color: transparent;
}



// content types and form displays

.user-logged-in{
  .page-title{
    word-break: break-word;
    line-height: 1.4;
  }
}
label{
  // margin: 0.5em 0;
}
#edit-actions{
  margin-bottom: 0;
}
.form--inline
,#edit-header
{
  margin-bottom: 2em;
}
.form-item{
  margin: 0.5em 0;
}
#search-block-form .form-item{
  margin: 0.5em 0;
}
ul.tips
,div.description
,.form-item .description
{
  color: $color-black-77;
  margin: .25em 0;
}
[id*="description"]{
  margin: .25em 0;
}
.field-multiple-table{
  thead
  ,tbody{
    tr{
      border-bottom: none;
    }
  }
  thead{
    th{
      color: $color-black-77;
      text-transform: none;
      padding: 0.5em 0;
      background: inherit;
      border: none;
    }
  }
}
.layout-region-node-footer__content{
  border-top: none;
}

#node-ucsfpharmacy-person-edit-form{
  .form-item{
    label+div{
      color: $color-text-gray;
    }
  }
}
  // keep certain fields close together because they are related or work well as a group
  .node-post-edit-form
  ,.node-event-edit-form
  ,.node-external-post-edit-form
  ,.node-in-the-news-edit-form
  {
    .layout-region{
      & > .field--name-title
      ,& > .field--name-field-event-series
      {
        margin-bottom: 0;
      }
    }
  }

.node-form{
  #edit-actions{
    .button:not(.button--danger){
      font-size: 1.5rem; // make certain buttons larger so they are easier to select
    }
  }
}
.form-item-title-0-value{
  input{
    font-size: 2em;
    padding: .1em .3em 0;
    width: 100%;
  }
}
.form-item-field-subtitle-0-value{
  input{
    font-size: 1.5em;
    letter-spacing: .05px;
    padding: 0 .3em;
    width: 100%;
  }
}
.form-item-body-0-summary{
  textarea{
    font-size: 1.25em;
  }
}
.node-event-form
,.node-event-edit-form
{
  .edit-group-dates
  ,.field--name-body
  ,.field--name-field-event-moreinfo
  {
    margin-top: 2em;
    margin-bottom: 2em;
  }
  #edit-field-event-location-0{
    padding: 0;
    border: none;
  }
  .form-item-field-event-location-0-title{
    margin-bottom: 0;
  }
  #field-event-datetime-values th{
    display: none;
  }
  #edit-field-event-moreinfo-wrapper{
    border: 1px solid #c0c0c0;
    padding: 0px 18px 6px;
    background-color: #fcfcfa;
  }
  .edit-field-organization-wrapper{
    margin-bottom: 3em;
  }
  fieldset#edit-field-event-location-0:not(.fieldgroup){
    padding: 0;
    border: none;
    & > legend{
      display: none;
    }
  }
  .form-item-field-event-audience-0-value{
    margin: 2.5em 0 2em;
    padding: 10px 12px;
    text-transform: uppercase;
    background-color: #f5f5f2;
    border: 1px solid #bfbfba;
  }
  #field-event-datetime-values fieldset:not(.fieldgroup){
    padding-top: 18px;
  }
  .form-item-field-event-audience-0-value{
    border: none;
    padding: 0;
    background-color: transparent;
  }
}
.media-library-add-form__preview {
  align-items: flex-start;
  background: unset;
}
.media-library-item--grid{
  &:before{
    border: none;
  }
  article{
    border: 1px solid var(--form-field-border-color);
    margin-bottom: 4px;
    & > .media-library-item__preview{
      margin-bottom: 0;
      padding: 0;
    }
  }
}
input[type='checkbox'] {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  & + label{
    vertical-align: middle;
    margin-left: 5px;
  }
}
.form-type-radio .description, .form-type-checkbox .description {
  margin-left: 30px;
}
fieldset:not(.fieldgroup){
  & > legend{
    position: initial;
  }
  padding: 10px 18px 18px;
}
.field--name-field-gallery-items{
  display: flex;
  flex-wrap: wrap;
  & > .field__item{
    max-width: 100px;
    margin: 0 1px 1px 0;
  }
}
.media-library-item__preview{
  .field--name-thumbnail{
    line-height: .8;
  }
}
.form-url{
  width: 100%;
}
.form-item-field-article-date-0-value-date
,.form-item-field-article-image-0-filefield-remote-url
{
  margin: 0;
}
input.form-autocomplete
,input.form-text
,input.form-tel
,input.form-email
,input.form-url
,input.form-search
,input.form-number
,input.form-color
,input.form-file
,input.form-date
,input.form-time
,textarea.form-textarea
{
  line-height: 1.45;
}
.form-item-field-article-image-0{
  .description{
    br{
      display: inline;
    }
  }
}
.image-preview{
  padding: 0 32px 32px 0;
}
.image-widget{
  border: none;
  padding: 0;
}
.image-widget-data{
  .form-item-field-media-image-0-alt{
    width: inherit;
    max-width: 70ch;
  }
  .file--image{
    margin-bottom: var(--margin-fields);
  }
  .button{
    margin-left: 0;
  }
}
.image-data__crop-wrapper{
  border: none;
  summary{
    display: none;
  }
  .seven-details__wrapper{
    padding: 0;
  }
}
.file-size{
  margin-right: var(--gap);
}
.entity-meta{
  // removed to resolve vertical tabs problem on http://d8.docksal.site/feed/add/node_event
  // margin: .75em 0;
}
tr{
  &.odd
  ,&.even
  {
    .form-item{
      &.text-format-wrapper
      ,&.form-type-textarea
      {
        margin-bottom: 0;
      }
    }
  }
}

// hide item weight info where irrelevant because there is only ever one item
.primary-media-flex-group
,.media-library-widget
{
  & > .fieldset-wrapper{
    .media-library-widget__toggle-weight{
      display: none;
    }
  }
}

// resolves too much vertical padding in .media-library-widget
fieldset:not(.fieldgroup).media-library-widget{
  padding: 0 18px;
}

// resolves incorrect position of remove button (X) for media items
.primary-media-flex-group{
  & > .fieldset-wrapper{
    .media-library-item__remove{
      top: 16px;
      right: 0;
    }
  }
}
.media-library-widget{
  & > .fieldset-wrapper{
    .media-library-item__remove{
      top: 10px;
      right: 10px;
    }
  }
}

// hide "The maximum number of media items have been selected."
.primary-media-flex-group
,.media-library-widget
{
  & > .fieldset-wrapper{
    .description{
      // display: none; // don't hide this, otherwise help text for name pronunciation will not appear
    }
  }
}

.primary-media-flex-group{
  &:not(.fieldgroup){
    padding: 0;
    background-color: transparent;
  }
  border: none;
  fieldset.form-wrapper:not(.fieldgroup)
  ,fieldset.form-item
  {
    margin: 0 0 1em 0;
  }
  & > legend{
    display: none; // label for fieldset group
  }
  & > .fieldset-wrapper{
    gap: var(--gutter);
    display: flex;
    flex-wrap: wrap;
    .field--name-field-caption{
      flex-grow: 1;
      .text-format-wrapper{
        margin: 0 calc( var(--gutter) * 2 ) 0 0;
        margin: 0;
      }
      .form-item-field-caption-0-value{
        margin-top: 0;
      }
    }
    .media-library-selection{
      margin: 0;
    }
    .media-library-item__preview{
      padding-bottom: 0;
    }
    .media-library-item--grid{
      padding: 0;
      width: 100%;
      .field--name-thumbnail{
        background: transparent;
      }
      &:before{
        border: none;
      }
    }
  }
}

// ief
.ief-entity-table{
  th{
    letter-spacing: 0.08em;
  }
  td{
    vertical-align: top;
  }
  input.button{
    margin-left: 0;
    margin-bottom: 0;
    &:first-child{
      margin-right: 1em;
    }
  }
  .field--name-field-media-image img
  ,.field--name-field-media-audio-file audio
  {
    max-width: 150px;
  }
  .field--name-field-media-link{
    word-break: break-all;
  }
}
.ief-row-form{
  .form-wrapper{
    & > div > div:not(.container-inline){
      margin-top: var(--gap);
      &.form-type-date
      ,&.form-textarea-wrapper
      ,&.description
      {
        margin: 0;
      }
    }
  }
}



// regions
.content-header{
  padding-top: 0; // save wasted space
}
.layout-region{
  & > .form-wrapper{
    margin-bottom: 36px;
  }
}



.display-modes{
  .block-system-main-block{
    th:first-child{
      width: 25ch;
    }
  }
}



// tabs
.is-collapse-enabled .tabs{
  max-height: unset;
}
.is-horizontal{
  left: 4px;
  width: calc( 100% - 4px );
}
.is-horizontal .tabs__tab.is-active
, .is-horizontal .tabs.primary .tabs__tab.is-active
, [dir="rtl"] .is-horizontal .tabs.primary .tabs__tab.is-active{
  border-bottom: 1px solid #fff;
}
.vertical-tabs__menu-item a{
  padding: 10px 15px;
  line-height: 1.4;
}
.vertical-tabs__menu-item.is-selected a{
  padding-left: 15px;
  font-size: 1.2em;
}
.vertical-tabs__menu-item.is-selected .vertical-tabs__menu-item-title{
  color: $color-black-77;
}

// tables
tbody tr:hover{
  background-color: $color-blue-10;
}
.field-multiple-table{
  tr{
    td:nth-child(2)
    ,td:nth-child(3)
    {
      padding-right: 0;
    }
  }
}

.field--widget-media-library-widget{
  fieldset:not(.fieldgroup){
    border: none;
    &.media-library-widget{
      padding: 0;
      margin-bottom: 0;
    }
    & > legend{
      text-transform: none;
      letter-spacing: 0;
    }
  }
}
.media-form .field--name-status {
  margin-top: 0;
}

[id^="field-event-audience-values"]{
  &.field-multiple-table{
    td{
      padding: 5px 6px;
    }
    tr.odd .form-item
    ,tr.even .form-item
    {
    margin-top: 0;
    margin-bottom: 0;
    }
  }
}

#field_ucsfphps_namepronounce-media-library-wrapper{
  padding: 1em;
  border: 1px solid $color-border;
}
.toolbar a{
  line-height: 1.2;
}